<template>
  <div class="menu-comp" v-if="isShowMenu">
    <img
      v-if="isCollapse"
      @click="handleCollapse(true)"
      :class="['level' + level]"
      class="collapse"
      src="../../assets/image/ScenarioOne/collapse.png"
      alt=""
    />
    <img
      v-else
      class="arrow-down"
      @click="handleCollapse(false)"
      :class="['level' + level, showSecondLevel ? 'level2' : '']"
      src="../../assets/image/ScenarioOne/arrow-down.png"
      alt=""
    />

    <!-- 一级菜单 -->
    <div class="first-level-menu" v-if="!isCollapse">
      <div class="box" @click="handleArrange">
        <img
          src="../../assets/image/ScenarioOne/first-level-menu/setting.png"
          alt=""
        />
        <span>布置</span>
      </div>
      <div class="box">
        <img
          src="../../assets/image/ScenarioOne/first-level-menu/camera.png"
          alt=""
        />
        <span>相机</span>
      </div>
    </div>

    <!-- 二级菜单 -->
    <second-menu
      v-if="showSecondLevel"
      @closeSecond="closeSecond"
      @clickSpecialEffectsItem="clickSpecialEffectsItem"
      @handleModel="handleModel"
      @handleRole="handleRole"
    ></second-menu>
  </div>
  <div class="moving-handle" v-show="!isShowMenu">
    <img src="../../assets/image/ScenarioOne/person-move.png" alt="">
  </div>
  <div class="slider-wrapper" v-show="isShowSlider">
    <div class="outer">
      <span>大小</span>
      <van-slider
        v-model="progressValue"
        bar-height="5px"
        button-size="12px"
        active-color="#0098ff"
        inactive-color="#2e7bafd6"
        @change="onprogressChange"
      />
    </div>
    <div class="close" @click="clickCloseSlider">
      <img src="../../assets/image/ScenarioOne/close.png" alt="">
    </div>
  </div>
  <!-- movement -->
  <movement v-show="!isShowMenu" :status="optionStatus"></movement>
  <!-- visual -->
  <visual v-show="!isShowMenu"></visual>
  <!-- EditBar -->
  <edit-bar v-show="!isShowMenu" @clickClose="clickClose" @clickEdit="clickEdit"></edit-bar>
</template>

<script>
import SecondMenu from "../secondMenu/index.vue";
import Movement from "../movement/index.vue"
import Visual from '../visual/index.vue'
import EditBar from '../editBar/index.vue'
import { defineComponent } from "vue";
import { ref } from "vue";
export default defineComponent({
  name: "MenuOption",
  emits: ["clickSpecialEffects", "clickItem", "closeSecond"],
  components: {
    SecondMenu,
    Movement,
    Visual,
    EditBar
  },
  setup(props, { emit }) {
    // 默认是折叠起来的
    let isCollapse = ref(true);
    let level = ref(0);
    let showSecondLevel = ref(false);
    let isShowMenu = ref(true);
    const progressValue = ref(55);
    const isShowSlider = ref(false)
    let optionStatus = ref(-1)

    const handleCollapse = (flag) => {
      isCollapse.value = !flag;
      level.value = flag ? 1 : 0;
      closeSecond();
    };

    const handleArrange = () => {
      showSecondLevel.value = true;
    };

    const closeSecond = () => {
      showSecondLevel.value = false;
      emit('closeSecond')
    };

    const clickSpecialEffectsItem = () => {
      console.log('clickSpecialEffectsItem')
      // 隐藏菜单选项
      isShowMenu.value = false;
      optionStatus.value = 3
      // 展示特效编辑选项
      emit('clickSpecialEffects');
    }

    const clickClose = () => {
      isShowMenu.value = true
      isShowSlider.value = false
      emit('closeSecond')
    }

    const onprogressChange = () => {
      console.log('onprogressChange')
    }

    const clickEdit = () => {
      isShowSlider.value = true
    }

    const clickCloseSlider = () => {
      isShowSlider.value = false
    }

    const handleModel = () => {
      optionStatus.value = 1
      isShowMenu.value = false;
      emit('clickItem')
    }

    const handleRole = () => {
      optionStatus.value = 2
      isShowMenu.value = false
      console.log('enter...', optionStatus.value)
      emit('clickItem')
    }

    return {
      isCollapse,
      level,
      showSecondLevel,
      isShowMenu,
      progressValue,
      isShowSlider,
      optionStatus,
      handleCollapse,
      handleArrange,
      closeSecond,
      clickSpecialEffectsItem,
      clickClose,
      onprogressChange,
      clickEdit,
      clickCloseSlider,
      handleModel,
      handleRole
    };
  },
});
</script>

<style lang="scss" scoped>
.menu-comp {
  bottom: 0px;
  width: 100%;
  height: 100px;
  .collapse,
  .arrow-down {
    width: 30px;
    position: fixed;
    right: 20px;
    bottom: 20px;
  }
  .level1 {
    bottom: 50px;
  }
  .level2 {
    bottom: 128px;
  }

  .first-level-menu {
    display: inline-block;
    display: flex;
    justify-content: flex-end;
    position: fixed;
    bottom: 20px;
    right: 20px;
    .box {
      background: rgba(79, 70, 57, 0.9);
      border-radius: 3px;
      border: 0.5px solid #fff;
      color: #fff;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 1rem;
      padding: 0.2rem 0.6rem;
      box-sizing: border-box;
      &:nth-child(1) {
        margin-right: 20px;
      }
      img {
        width: 20px;
        margin-right: 4px;
      }
    }
  }
}
.moving-handle {
  width: 4.375rem;
  height: 4.375rem;
  position: absolute;
  left: 5.75rem;
  bottom: 3.75rem;
  img {
    width: 100%;
    height: 100%;
  }
}
.slider-wrapper {
  display: flex;
  width: 13.75rem;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 3.75rem;
  .outer {
    background-image: url(../../assets/image/ScenarioOne/first-level-menu/bg.png);
    background-repeat: no-repeat;
    background-size: 100% 100%;
    width: 14rem;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 1.8rem;
  }
  span {
    color: #fff;
    font-size: 0.75rem;
    margin-right: 4px;
  }
  .van-slider {
    width: 10rem;
  }
  .close {
    width: 1.5rem;
    height: 1.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #000;
    border: 0.0625rem solid #ccc;
    box-sizing: border-box;
    margin-left: 4px;
    img {
      width: 0.81rem;
      height: 0.81rem;
    }
  }
}
</style>