
import { defineComponent, ref } from "vue";
export default defineComponent({
  name: "Visual",
  setup(props, { emit }) {
    let visualType = ref(0)

    const handleVisual = (val:number) => {
      if (val === visualType.value) {
        visualType.value = 0
        return
      }
      visualType.value = val
    }
    return {
      visualType,
      handleVisual
    };
  },
});
