
import { defineComponent } from 'vue'
import HeadComp from '@/components/head/index.vue'
import Lighting from '@/components/lighting/index.vue'
import MenuOption from '@/components/menu/index.vue'
import { ref } from 'vue'
export default defineComponent({
  name: 'App',
  components: {
    HeadComp,
    Lighting,
    MenuOption
  },
  setup(props, { emit }) {
    let isShowLight = ref(true);

    const clickItemSE = () => {
      isShowLight.value = false
    }

    const handleClickItem = () => {
      isShowLight.value = false
    }

    const handleClose = () => {
      isShowLight.value = true
    }
    return {
      isShowLight,
      clickItemSE,
      handleClickItem,
      handleClose
    }
  }
})
