
import { defineComponent } from "vue";
import { ref } from "vue";
enum lightType {
  Morning = "morning",
  Evening = "evening",
  Night = "night",
}
export default defineComponent({
  name: "Lighting",
  setup() {
    let isLighting = ref(false);
    let lightType = ref("");

    const handleLight = () => {
      isLighting.value = !isLighting.value;
      if (isLighting) {
        lightType.value = 'morning'
      }
    };

    const handleOpt = (type: string) => {
      lightType.value = type;
    };

    return {
      isLighting,
      lightType,
      handleLight,
      handleOpt,
    };
  },
});
