<template>
  <div class="second-menu-comp">
    <div class="top-tabbar">
      <div class="close">
        <van-icon name="cross" @click="handleClose" />
      </div>
      <ul>
        <li
          v-for="item in type"
          :key="item.id"
          :class="['tab-btn', { activeType: currentTypeIndex === item.id }]"
          @click="changeType(item.id)"
        >
          {{ item.name }}
        </li>
      </ul>
      <div class="search">
        <input v-model="seachValue" type="text" placeholder="搜索" />
        <img
          src="../../assets/image/ScenarioOne/second-menu/search.png"
          alt=""
        />
      </div>
    </div>
    <div class="menu-main">
      <!-- 模型 -->
      <div class="model" v-if="currentTypeIndex === 1">
        <div class="list-type">
          <div
            v-for="item in typeModel"
            :key="item.id"
            :class="{ active: currentBtnIndex === item.id }"
            @click="changeModel(item.id)"
          >
            <van-icon class-prefix="icon" :name="item.icon" class="iconfont" />
            <span style="margin-top: 1px">{{ item.name }}</span>
          </div>
        </div>
        <div class="scroll-main">
          <ul class="scroll2">
            <li
              v-for="item in dataModel"
              :key="item.uid"
              :class="{ activeItem: currentListIndex === item.uid }"
              @click="changeList(item.uid)"
            >
              <img :src="item.img" alt="" />
              <span>{{ item.name }}</span>
            </li>
          </ul>
        </div>
      </div>
      <!-- 角色 -->
      <div class="role" v-if="currentTypeIndex === 2">
        <div class="list-type">
          <span>
            <img
              src="@/assets/image/ScenarioOne/third-menu/icon.png"
              alt=""
              srcset=""
            />
            鬼刀
          </span>
        </div>
        <div class="scroll-main">
          <ul class="scroll2">
            <li
              v-for="item in dataRole"
              :key="item.uid"
              :class="{ activeItem: currentListIndex === item.uid }"
              @click="changeList(item.uid)"
            >
              <img :src="item.img" alt="" />
              <span>{{ item.name }}</span>
            </li>
          </ul>
        </div>
      </div>
      <!-- 特效 -->
      <div class="specialEffects" v-if="currentTypeIndex === 3">
        <div class="scroll-main">
          <ul class="scroll2">
            <li
              v-for="item in dataEffect"
              :key="item.uid"
              :class="{ activeItem: currentListIndex === item.uid }"
              @click="changeList(item.uid)"
            >
              <img :src="item.img" alt="" />
              <span>{{ item.name }}</span>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { defineComponent, onMounted } from "vue";
import { ref } from "vue";
import icon from "@/assets/image/ScenarioOne/bg.jpg";
export default defineComponent({
  name: "SecondMenu",
  setup(props, { emit }) {
    let seachValue = ref("");
    let showThirdLevel = ref(false);

    let currentTypeIndex = ref(1);
    let currentBtnIndex = ref(1);
    let currentListIndex = ref(null);

    let type = ref([
      { id: 1, name: "模型" },
      { id: 2, name: "角色" },
      { id: 3, name: "特效" },
    ]);
    let typeModel = ref([
      { id: 1, name: "装饰", icon: "tupiantianjia" },
      { id: 2, name: "地编", icon: "kapianzhanshi" },
      { id: 3, name: "家具", icon: "shafa" },
      { id: 4, name: "建筑", icon: "architectrue" },
    ]);
    let dataModel = ref([]);
    let dataRole = ref([]);
    let dataEffect = ref([]);

    const handleClose = () => {
      emit("closeSecond");
    };
    // 切换类型
    const changeType = (id) => {
      currentTypeIndex.value = id;
      currentListIndex.value = null;
      console.log(id);
    };
    // 模型
    const changeModel = (id) => {
      console.log(id);
      currentBtnIndex.value = id;
      currentListIndex.value = null;
      dataModel.value.forEach((item, index) => {
        switch (id) {
          case 2:
            item.name = `地编${index + 1}`;
            break;
          case 3:
            item.name = `家具${index + 1}`;
            break;
          case 4:
            item.name = `建筑${index + 1}`;
            break;
          default:
            item.name = `装饰${index + 1}`;
        }
        return item;
      });
    };
    const changeList = (id) => {
      console.log(id);
      currentListIndex.value = id;
      console.log('当前是什么类型：', currentTypeIndex.value)
      switch (currentTypeIndex.value) {
        case 1:
          handleModel()
          break;
        case 2:
          handleRole()
          break;
        case 3:
          handleSpecialEffects()
          break;
        default:
          break;
      }
    };
    const getData = () => {
      let arr1 = [];
      let arr2 = [];
      let arr3 = [];
      for (let i = 1; i <= 10; i++) {
        arr1.push({ uid: i, name: `装饰${i}`, img: icon });
      }
      for (let i = 1; i <= 10; i++) {
        arr2.push({ uid: i, name: `角色${i}`, img: icon });
      }
      for (let i = 1; i <= 10; i++) {
        arr3.push({ uid: i, name: `特效${i}`, img: icon });
      }
      dataModel.value = arr1;
      dataRole.value = arr2;
      dataEffect.value = arr3;
    };

    // 角色

    onMounted(() => {
      getData();
      console.log("Component is mounted!");
    });

    // 处理选中模型
    const handleModel = () => {
      emit('handleModel');
    }

    // 处理角色选中
    const handleRole = () => {
      emit('handleRole');
    }

    // 处理选中特效
    const handleSpecialEffects = () => {
      // 显示特效编辑选项
      console.log('handleSpecialEffects')
      emit('clickSpecialEffectsItem');
    }

    return {
      seachValue,
      type,
      typeModel,
      currentBtnIndex,
      currentListIndex,
      currentTypeIndex,
      dataModel,
      dataRole,
      dataEffect,
      handleClose,
      changeType,
      changeModel,
      changeList,
    };
  },
});
</script>

<style lang="scss" scoped>
.second-menu-comp {
  width: 100%;
  position: fixed;
  bottom: -2px;
  .top-tabbar {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding-right: 20px;
    .close {
      margin-left: 20px;
      i {
        color: #fff;
        font-size: 1.5rem;
      }
    }
    ul {
      display: flex;
      color: #fff;
      li {
        font-size: 0.9rem;
        padding: 0.2rem 1.2rem;
        background: #000c;
        border: 1px solid #585959;
        margin-left: 0.5rem;
        border-radius: 2px;
      }
      li.activeType {
        background: #093f63;
        border: 1px solid #0371bb;
      }
    }
    .search {
      margin-left: 1rem;
      padding: 0px 1rem;
      width: 6rem;
      color: #fff;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 14px;
      box-sizing: border-box;
      height: 1.8rem;
      border-radius: 2px;
      border: 1px solid #085890;
      background: #5f5959;
      input {
        width: 100%;
        background: transparent;
        outline: none;
        box-shadow: none;
        border: none;
        color: #ccc;
      }
      input::-webkit-input-placeholder {
        font-size: 12px;
        color: #ccc;
      }
      input:-moz-placeholder {
        font-size: 12px;
        color: #ccc;
      }
      input:-ms-input-placeholder {
        font-size: 12px;
        color: #ccc;
      }
      img {
        width: 20px;
        position: relative;
        left: 0px;
      }
    }
  }

  .menu-main {
    background-color: rgba(0, 0, 0, 0.7);
    height: 102px;
    .list-type {
      width: 100px;
      display: flex;
      flex-wrap: wrap;
      padding: 5px 5px 0;
      justify-content: space-around;
      float: left;
      border-right: 1px solid #838383;
      .active {
        color: #1889fa;
      }
      div {
        display: flex;
        flex-direction: column;
        justify-content: center;
        font-size: 12px;
        align-items: center;
        width: 45%;
        color: #fff;
        margin-bottom: 3px;
        background-image: url(../../assets/image/ScenarioOne/third-menu/bg.png);
        background-size: 100% 100%;
        background-repeat: no-repeat;
        box-sizing: border-box;
        padding: 3px;
        img {
          width: 20px;
          margin-bottom: 2px;
        }
      }
    }
    .scroll-main {
      overflow-x: auto;
      margin-left: 20px;
      height: 102px;
      &::before {
        width: 1px;
        background-color: #ccc;
        position: absolute;
        content: "";
        display: block;
      }
      .scroll2 {
        padding: 0.8rem 0;
        display: flex;
        color: #fff;
        width: 1500px;
        height: 100%;
        li {
          width: 8rem;
          text-align: center;
          position: relative;
          margin-left: 1rem;
          border-radius: 3px;
          border: 1px solid #333;
          height: 73%;
          img {
            width: 100%;
            border-radius: 3px;
            height: 100%;
          }
          span {
            position: absolute;
            bottom: 0;
            display: block;
            width: 100%;
            background: #333333e6;
            font-size: 0.6rem;
            padding-top: 0.2rem;
          }
        }
        li.activeItem {
          border: 1px solid #0371bb;
          span {
            background: #093f63cc;
          }
        }
      }
    }
    .role {
      .list-type {
        height: 98px;
        color: #4387cb;
        span {
          display: block;
          border: 1px solid #4387cb;
          height: 30px;
          line-height: 30px;
          text-align: center;
          width: 80px;
          background: #3e3e3e;
          border-radius: 2px;
        }
        img {
          width: 25px;
          vertical-align: middle;
        }
      }
    }
  }
}
</style>